import React from "react";
import {Link} from "react-router-dom";

const MovieP = props =>{
    return(
      <div>
        <div className={'d-flex margin'}>
            <div className={'imgDiv'}>
                <img className={'poster'} src={`https://image.tmdb.org/t/p/w500${ props.activeMovie.poster_path}`} alt={props.activeMovie.title}/>
            </div>
            <div>
                <h2>{props.activeMovie.title}</h2>
                <h4>{props.activeMovie.tagline}</h4>
                <div>{props.activeMovie.overview}</div>
                <h3>Rating: {props.activeMovie.vote_average}</h3>
                <h3>Budget: {props.activeMovie.budget}$</h3>
                <h3>Status: {props.activeMovie.status}</h3>
                <h3>Release Date: {props.activeMovie.release_date}</h3>


            </div>
        </div>
      </div>
    );
};

export default MovieP;