import React, {Component} from 'react';


import './App.css';

import Movies from "./Components/movie";
import Btn from "./Components/button";
import {Link} from "react-router-dom";
import Cart from "./Components/Cart";
import Popup from "./Components/PopUp";
import PopMovie from "./Components/pop";
import Search from "./Components/Search";
import {ThemeContext} from "./Components/darkMode";
import Loader from "./Components/Loader/Loader";



const apiKey = '34c6dd52b40998a7fba114d180097301';




class App extends Component{

  state={
    results:[],
    page:1,
    totalPages:null,
    moviesWillWatch: [],
    showPopup: false,
    isLoaded:false,
    isLoadedSearch:false,
    search:false,
    input:null
  };

  static contextType = ThemeContext;

  nextPage=()=>{
      if(this.state.page!==500){
        this.setState({page:this.state.page+1});


      }
    console.log(this.state.page);
    };
  previousPage=()=>{
      if (this.state.page!==1) {
        this.setState({page:this.state.page-1});
      }
    };
  fetchSearch = async()=>{
    if (this.state.input) {
      const apiCall = await fetch(`https://api.themoviedb.org/3/search/movie?api_key=${apiKey}&language=en-US&query=${this.state.input}&page=${this.state.page}&include_adult=false`);
      const data = await apiCall.json();
      this.setState({results: data.results, totalPages: data.total_pages, isLoadedSearch:false});
      console.log(data);
    }
  };

  fetchSearchData = async(e)=>{
    this.setState({isLoadedSearch:true, search:true, page:1});
    const input = e.target.elements.input.value;

    e.preventDefault();
    await this.setState({input:input});
    console.log(this.state.input);
    await this.fetchSearch();
  };


  fetchData = async()=>{

      const apiCall = await fetch(`https://api.themoviedb.org/3/movie/popular?api_key=${apiKey}&language=en-US&page=${this.state.page}`);
      const data = await apiCall.json();
      this.setState({results:data.results, totalPages:data.total_pages, isLoaded:true});
      console.log(data);

  };

  componentDidMount = ()=> {
      this.fetchData();
  };

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevState.page!==this.state.page){
      if (this.state.search){
        this.fetchSearch();
      }else{
        this.fetchData();
      }

    }
  }

  addMovieToWillWatch = (results) => {

    const updateMoviesWillWatch = [...this.state.moviesWillWatch];
    updateMoviesWillWatch.push(results);

    this.setState({
      moviesWillWatch: updateMoviesWillWatch
    });

    console.log(this.state.moviesWillWatch)
  };

  removeMovieFromWillWatch = movie => {
    const updateMoviesWillWatch = this.state.moviesWillWatch.filter(function(item){
      return item.id !== movie.id
    });

    this.setState({
      moviesWillWatch: updateMoviesWillWatch
    })
  };

  togglePopup=()=>{
    this.setState({
      showPopup: !this.state.showPopup
    });
  };



  render() {
    const {darkMode,changeTheme} = this.context;
   return (
     <div className={`body  ${darkMode?'darkBody':''} `}>
      <div className={`App container marginAuto position-relative ${darkMode?'darkBodySite':''}`}>
        <nav className={`navbar sticky-top navbar-light bg-light marginAuto header red d-flex justify-content-around ${darkMode?'darkHeader':''}`}>
            <h2 className={'HeaderText'}>MovieApp</h2>
            <Btn nextPage={this.nextPage} previousPage={this.previousPage} />
            <button className={darkMode?'btn btn-secondary':'btn btn-warning'} onClick={()=>{changeTheme()}}>
              {darkMode? <i className="fas fa-sun"/>: <i className="fas fa-moon"/>}
            </button>
            <Cart moviesWillWatch={this.state.moviesWillWatch} togglePopup={this.togglePopup}/>
            {this.state.showPopup ?
              <Popup
                closePopup={this.togglePopup}
                moviesWillWatch={this.state.moviesWillWatch}
              />
              : null
            }

        </nav>
        <Search fetchSearchData={this.fetchSearchData} isLoadedSearch={this.state.isLoadedSearch}/>
        <React.Fragment>
          <div className={'movies width-90 marginAuto'}>
            {this.state.isLoaded?'':<Loader/>}
            {this.state.results.map((results) => {
               return(
                 <Movies  results={results}
                         key={results.id}
                         addMovieToWillWatch={this.addMovieToWillWatch}
                         removeMovieFromWillWatch={this.removeMovieFromWillWatch}
                 />
                 )
            })
            }</div>
        </React.Fragment>

      </div>
     </div>
  );
 }
}

export default App;
